.Burger{
	margin : auto;
	width : 100%;
	height: 250px;
	overflow: scroll;
	text-align: center;
	font-weight: bold; 
	font-size: 1.2rem;
}

@media (min-width: 500px) and (min-height: 400px){
	.Burger{
		width: 200px;
		height:  250px;
	}
}

@media (min-width: 500px) and (min-height: 401px){
	.Burger{
		width: 250px;
		height:  250px;
	}
}

@media (min-width: 1000px) and (min-height: 700px){
	.Burger{
		width: 350px;
		height:  400px;
	}
}